import React, { useState } from 'react';
import { connect } from 'react-redux';
import NoSSR from '@mpth/react-no-ssr';
import { StyledDetails, StyledDetailsImg, StyledDetailsList, StyledDetailsListItem, StyledName } from '../../people/peopleInfo/peopleInfo.styled';
import ErrorMessage from "../../errorMessage/errorMessage";
import Spinner from '../../spinner/spinner';
import { setBroken } from '../../../actions'

const StarshipsInfo = ({ selectedStarship, isFetchingStarship, setBroken, broken}) => {
  const serverImage =
    <StyledDetailsImg
      onError={() => setBroken()}
      src={`https://starwars-visualguide.com/assets/img/starships/${selectedStarship.id}.jpg`} />
  const brokenImg = broken ? <ErrorMessage /> : serverImage;


  return (
    <>
      <StyledDetails>
        {isFetchingStarship ? <Spinner /> :
          <>
            <StyledName>{selectedStarship.name}</StyledName>
            {brokenImg}
            <StyledDetailsList>
              <StyledDetailsListItem>Model:{selectedStarship.model}</StyledDetailsListItem>
              <StyledDetailsListItem>Manufacturer: {selectedStarship.manufacturer}</StyledDetailsListItem>
              <StyledDetailsListItem>Cost: {selectedStarship.cost_in_credits}</StyledDetailsListItem>
              <StyledDetailsListItem>Length: {selectedStarship.length}</StyledDetailsListItem>
              <StyledDetailsListItem>Max speed: {selectedStarship.max_atmosphering_speed}</StyledDetailsListItem>
              <StyledDetailsListItem>Passengers: {selectedStarship.passengers}</StyledDetailsListItem>
            </StyledDetailsList>

          </>
        }
      </StyledDetails>
    </>
  )
}
const mapStateToProps = (state) => {
  return {
    selectedStarship: state.selectedStarship,
    isFetchingStarship: state.isFetchingStarship,
    broken: state.broken
  }
}



export default connect(mapStateToProps, {setBroken})(StarshipsInfo);